<!--全部、来信、来访、网信切换-->
<template>
  <ul class="typeChange">
    <li
      v-for="(item, index) in arr"
      :key="index"
      @click="changeType(item)"
      :class="curType === item.name ? 'active' : ''"
    >{{item.name}}
    </li>
  </ul>
</template>

<script>
  export default {
    name: "index",
    data() {
      return {
        arr: [
          {
            "name": "参评率",
            "id": "参评率"
          },
          {
            "name": "满意率",
            "id": "满意率"
          },
          {
            "name": "及时受理率",
            "id": "及时受理率"
          }
        ],
        curType: "参评率",
        curTypeId: "参评率",
      }
    },
    // mounted(){
    //   this.$emit("change", this.curTypeId);
    // },
    methods: {
      changeType(item) {
        this.curType = item.name;
        this.curTypeId = item.id;
        this.$emit("change", this.curTypeId);
      }
    }
  }
</script>

<style lang="stylus" scoped>
  .typeChange
    display flex
    justify-content center
    flex-direction column
    padding-top .2rem
    li
      width 1.4rem
      border 1px #4295dc solid
      text-align center
      line-height 1.8
      margin:.1rem 0
      border-radius .2rem
      color #fff
      font-size 0.16rem
      cursor pointer
      background rgba(000,000,000,.7)
      &.active
        color #8df6ff
        font-weight 700
        text-shadow 0 0 5px #0cafc9
</style>
