<template>
  <div class="wrapper">
    <div class="w-seach" @click="handlePopShow">
      <p :title="curName.length>6?curName:''">{{curName}}</p>
    </div>
    <transition name="fade">
      <unitSeach v-if="popShow" :name="curName" @changepop="popClick"></unitSeach>
    </transition>
    <div class="content">
      <div class="c-top">
        <div class="w-charts">
          <ctitle :title="'参评率'"></ctitle>
          <div class="wxbfb">{{cplwxbfb}}%</div>
          <div class="charts">
            <pie-chart3D :params="cplParams"></pie-chart3D>
            <!-- <meiguiPie :params="cplParams"></meiguiPie> -->
          </div>
        </div>
        <div class="w-charts">
          <ctitle :title="'满意率'"></ctitle>
          <div class="wxbfb">{{mylwxbfb}}%</div>
          <div class="charts">
            <pie-chart3D :params="mylParams"></pie-chart3D>
            <!-- <meiguiPie :params="mylParams"></meiguiPie> -->
          </div>
        </div>
        <div class="w-charts">
          <ctitle :title="'及时受理率'"></ctitle>
          <div class="wxbfb">{{jssllwxbfb}}%</div>
          <div class="charts">
            <pie-chart3D :params="jssllParams"></pie-chart3D>
            <!-- <meiguiPie :params="jssllParams"></meiguiPie> -->
          </div>
        </div>
        <!--<div class="w-charts">-->
        <!--<ctitle :title="'网信占比'"></ctitle>-->
        <!--<div class="charts">-->
        <!--<meiguiPie :params="wxzbParams"></meiguiPie>-->
        <!--</div>-->
        <!--</div>-->
      </div>
      <div class="c-bottom">
        <!--<div class="c-t-left">-->
          <!--<ctitle :title="'工程过程监控'"></ctitle>-->
          <!--<div class="gcjk">-->
            <!--<gcgcjkDiv :data="gzgcjkData"></gcgcjkDiv>-->
          <!--</div>-->
        <!--</div>-->
        <div class='c-t-right'>
          <ctitle :title="'责任单位排名'"></ctitle>
          <div class="w-charts">
            <div class='btn'>
              <blzlBtn @change="changeBar"></blzlBtn>
            </div>
            <div class='chart'>
              <barline :params="xflphBarParams"></barline>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  import { get_http } from "@/api/index";
  import ctitle from "@/components/title/index";
  import meiguiPie from "@/components/pie/meiguiPie";
  import gcgcjkDiv from "@/components/gcgcjk/index";
  import blzlBtn from "@/components/qhpmBtn/lfBtn";
  import unitSeach from "@/components/unitSeach/index";
  import barline from "@/components/bar/barLine2";
  import PieChart3D from "@/components/pie/PieChart3D1";

  export default {
    name: "allxfbm",
    components: {
      ctitle,
      gcgcjkDiv,
      meiguiPie,
      blzlBtn,
      unitSeach,
      barline,
      PieChart3D
    },
    data() {
      return {
        Cqh: '广东省',
        cplwxbfb: 0,
        mylwxbfb: 0,
        jssllwxbfb: 0,
        popShow: false,
        curName: '广东省-广东省统计局',
        curCqh:'广东省',
        curTypeId: "网信",
        // 工程过程监控
        gzgcjkData:[],
        // 责任单位排名
        zrdwBarBtn:'参评率',
        xflphBarParams: {
          data: [],
          canvasStyle: {
            width: "100%",
            height: "100%"
          },
          options: {
            // isVertical:true,
            xName: "name", //x轴字段
            axisValueShow: false,
            categoryBr: true,
            axis1Name: "人次",
            axisLabelSize: 14, // label字号
            left: "0%",
            right: "5%",
            top: "5%",
            bottom: "0%",
            barLabelOptions: "top",
            barWidth: "25%",
            barBorderRadius: 10,
            splitLineShow: false,
            barLabelShow: false, //柱状图数量值显示
            axisLabelColor: "rgba(0, 176, 255, .8)",
            itemLabelColor: "rgba(0, 176, 255, .8)",
            colors: {
              bar: [["#7EE0FF", "#036CF5"]]
            },
            map: {
              value: "参评率"
            }
          }
        },
        // 参评
        cplParams: {
          data: [],
          // color: this.$STANDARD.pieColors,
          color: ['rgb( 154, 5, 245 )', 'rgb( 235, 131, 124 )', 'rgb( 68, 240, 233 )', 'rgb( 12, 129, 254 )',],   //环形颜色
          angle: 20,
          radius: "30%",
          labelRadius: 30,
          styleObj: {
            width: "100%",
            height: "100%"
          },
          isBr: true,
          labelPer: true
          // wxbfb:'', //标题百分比
          // canvasStyle: {
          //   width: '100%',
          //   height: '100%'
          // },
          // options: {
          //   colors:['rgb( 154, 5, 245 )','rgb( 235, 131, 124 )','rgb( 68, 240, 233 )','rgb( 12, 129, 254 )',],   //环形颜色
          //   unti: '件',
          //   labelColor: '#fff',
          //   labelFontSize: 16, //21
          //   labelLineSmooth: 0,
          //   labelLineHeight: 23
          // },
          // data: [],
        },
        // 满意
        mylParams: {
          data: [],
          // color: this.$STANDARD.pieColors,
          color: ['rgb( 154, 5, 245 )', 'rgb( 235, 131, 124 )', 'rgb( 68, 240, 233 )', 'rgb( 12, 129, 254 )',],   //环形颜色
          angle: 20,
          radius: "30%",
          labelRadius: 30,
          styleObj: {
            width: "100%",
            height: "100%"
          },
          isBr: true,
          labelPer: true
          // wxbfb:'', //标题百分比
          // wxbfbAll:'', //标题总数
          // canvasStyle: {
          //   width: '100%',
          //   height: '100%'
          // },
          // options: {
          //   colors:['rgb( 154, 5, 245 )','rgb( 235, 131, 124 )','rgb( 68, 240, 233 )','rgb( 12, 129, 254 )',],   //环形颜色
          //   unti: '件',
          //   labelColor: '#fff',
          //   labelFontSize: 16, //21
          //   labelLineSmooth: 0,
          //   labelLineHeight: 23
          // },
          // data: [],
        },
        // 及时受理
        jssllParams: {
          data: [],
          // color: this.$STANDARD.pieColors,
          color: ['rgb( 154, 5, 245 )', 'rgb( 235, 131, 124 )', 'rgb( 68, 240, 233 )', 'rgb( 12, 129, 254 )',],   //环形颜色
          angle: 20,
          radius: "30%",
          labelRadius: 30,
          styleObj: {
            width: "100%",
            height: "100%"
          },
          isBr: true,
          labelPer: true
          // wxbfb:'', //网信占比标题百分比
          // wxbfbAll:'', //网信占比标题总数
          // canvasStyle: {
          //   width: '100%',
          //   height: '100%'
          // },
          // options: {
          //   colors:['rgb( 154, 5, 245 )','rgb( 235, 131, 124 )','rgb( 68, 240, 233 )','rgb( 12, 129, 254 )',],   //环形颜色
          //   unti: '件',
          //   labelColor: '#fff',
          //   labelFontSize: 16, //21
          //   labelLineSmooth: 0,
          //   labelLineHeight: 23
          // },
          // data: [],
        },
        // 网信占比
        // wxzbParams: {
        //   wxbfb:'', //标题百分比
        //   wxbfbAll:'', //标题总数
        //   canvasStyle: {
        //     width: '100%',
        //     height: '100%'
        //   },
        //   options: {
        //     colors:['rgb( 154, 5, 245 )','rgb( 68, 240, 233 )','rgb( 12, 129, 254 )','rgb( 235, 131, 124 )'],   //环形颜色
        //     unti: '件',
        //     labelColor: '#fff',
        //     labelFontSize: 16, //21
        //     labelLineSmooth: 0,
        //     labelLineHeight: 23
        //   },
        //   data: [],
        // },
      };
    },
    mounted() {
      this.getAPI(this.date,this.curName)
    },
    methods: {
      // 点击部门索引触发
      handlePopShow() {
        this.popShow = true;
      },
      // 点击弹框确认按钮后触发
      popClick(name, erjiName){
        if (name === '') {
          this.curName = '单位索引';
        } else {
          if (erjiName === '') {
            this.curName = name;
          } else {
            this.curName = name + '-' + erjiName;
          }
        }
        this.curCqh=name;
        this.Cqh = name;
        console.log(this.curCqh);
        console.log(this.curName);

        this.getAPI(this.date, this.curName);
      },
      // 市级信访部门排名TOP10按钮
      changeBar(id) {
        this.zrdwBarBtn = id;
        this.xflphBarParams.options.map = {value: id};
        this.getBarAPI(this.date,this.curName);
      },
      getAPI(date, name) {
        let unitParam;
        if (name.split("-")[1] == undefined || name.split("-")[1] === '') {
          unitParam = 'drop';
        } else {
          unitParam = name.split("-")[1];
        }

        // 1工作过程监控
        // get_http("xfjxkhZrdw", {
        //   gd_id3: '工作过程监控',
        //   zrdw: unitParam,
        //   xfxs:this.curTypeId,
        //   day: `[${date.startDate},${date.endDate}]`
        // })
        //   .then(res => {
        //     this.gzgcjkData = res.data.data;
        //   })
        //   .catch(err => {
        //     console.log(err);
        //   });

        // 2参评
        get_http("xfjxkhZrdw", {
          gd_id3: '参评饼图',
          zrdw: unitParam,
          xfxs:this.curTypeId,
          Cqh: this.Cqh,
          day: `[${date.startDate},${date.endDate}]`
        })
          .then(res => {
            this.cplParams.data = res.data.data;
            get_http("xfjxkhZrdw", {
              gd_id3: '参评率',
              zrdw: unitParam,
              xfxs:this.curTypeId,
              Cqh: this.Cqh,
              day: `[${date.startDate},${date.endDate}]`
            })
              .then(res => {
                this.cplwxbfb = (res.data.data[0].value*100).toFixed(2);
              })
              .catch(err => {
                console.log(err);
              });
          })
          .catch(err => {
            console.log(err);
          });

        // 3满意
        get_http("xfjxkhZrdw", {
          gd_id3: '满意饼图',
          zrdw: unitParam,
          xfxs:this.curTypeId,
          Cqh: this.Cqh,
          day: `[${date.startDate},${date.endDate}]`
        })
          .then(res => {
            this.mylParams.data = res.data.data;
            get_http("xfjxkhZrdw", {
              gd_id3: '满意率',
              zrdw: unitParam,
              xfxs:this.curTypeId,
              Cqh: this.Cqh,
              day: `[${date.startDate},${date.endDate}]`
            })
              .then(res => {
                this.mylwxbfb = (res.data.data[0].value*100).toFixed(2);
              })
              .catch(err => {
                console.log(err);
              });
          })
          .catch(err => {
            console.log(err);
          });

        // 4及时受理
        get_http("xfjxkhZrdw", {
          gd_id3: '及时受理饼图',
          zrdw: unitParam,
          xfxs:this.curTypeId,
          Cqh: this.Cqh,
          day: `[${date.startDate},${date.endDate}]`
        })
          .then(res => {
            this.jssllParams.data = res.data.data;
            get_http("xfjxkhZrdw", {
              gd_id3: '及时受理率',
              zrdw: unitParam,
              xfxs:this.curTypeId,
              Cqh: this.Cqh,
              day: `[${date.startDate},${date.endDate}]`
            })
              .then(res => {
                this.jssllwxbfb = (res.data.data[0].value*100).toFixed(2);
              })
              .catch(err => {
                console.log(err);
              });
          })
          .catch(err => {
            console.log(err);
          });
        // 5网信占比
        // get_http("xfjxkhZrdw", {
        //   gd_id3: '网信占比饼图',
        //   zrdw: unitParam,
        //   xfxs:this.curTypeId
        // })
        //   .then(res => {
        //     this.wxzbParams.data = res.data.data;
        //     get_http("xfjxkhZrdw", {
        //       gd_id3: '网信占比率',
        //       zrdw: unitParam,
        //       xfxs:this.curTypeId
        //     })
        //       .then(res => {
        //         this.wxzbParams.wxbfb = (res.data.data[0].value*100).toFixed(2);
        //       })
        //       .catch(err => {
        //         console.log(err);
        //       });
        //   })
        //   .catch(err => {
        //     console.log(err);
        //   });
        this.getBarAPI(date,unitParam);
      },
      // 6.责任单位排名TOP10柱图
      getBarAPI(date,unitParam){
        get_http("xfjxkhZrdw", {
          gd_id3: '排名',
          gd_id4:this.zrdwBarBtn,
          xfxs:this.curTypeId,
          // Cqh:this.curCqh,
          Cqh: this.Cqh,
          day: `[${date.startDate},${date.endDate}]`
        })
          .then(res => {
            this.xflphBarParams.data = res.data.data;
          })
          .catch(err => {
            console.log(err);
          });
      }
    },
    computed: {
      date() {
        return this.$store.state.date;
      }
    },
    watch: {
      date(val) {
        this.getAPI(val, this.curName);
      }
    }
  };
</script>

<style lang="stylus" scoped>
  .wrapper
    position relative
    top 0.8rem
    height calc(100% - 0.8rem)

    .fade-enter-active, .fade-leave-active
      transition opacity 0.3s

    .fade-enter, .fade-leave-to
      opacity 0

    .w-seach
      position absolute
      top -75px
      right 150px
      z-index 1000
      box-sizing border-box
      padding 0 10px
      line-height 50px
      font-size 22px
      color #cbfbff
      background url('../../../assets/popbtn-bg.png')
      background-size auto 100%
      cursor pointer
      &:before
        content ''
        position absolute
        top 0
        left -34px
        width 34px
        height 50px
        background url('../../../assets/popbtn-bg-l.png')
        background-size 100% 100%
      &:after
        content ''
        position absolute
        top 0
        right -34px
        width 34px
        height 50px
        background url('../../../assets/popbtn-bg-r.png')
        background-size 100% 100%
    .content
      width 100%
      height 100%
      display flex
      padding 0.2rem 0.3rem
      flex-direction column
      box-sizing border-box
      .c-top
        height 50%
        display flex
        box-sizing border-box
        flex-direction row
        .w-charts
          flex 1
          flex-direction column
          height 100%
          display flex
          position relative
          .wxbfb
            font-size 0.18rem
            color #fff
            font-weight bolder
            position absolute
            top 46px
            left 66px
          .charts
            flex 1
            display flex
            width 100%
            height 90%
      .c-bottom
        height 50%
        position relative
        box-sizing border-box
        display flex
        .c-t-left
          flex 1
          box-sizing border-box
          .gcjk
            position relative
        .c-t-right
          flex 1
          box-sizing border-box
          display flex
          flex-direction column
          .w-charts
            flex 1
            display flex
            flex-direction row
            .btn
              flex .2
              display flex
              height 100%
              align-items center
              justify-content center
            .chart
              flex .8
              display flex
              height 100%
              align-items center
              justify-content center

    .el-loading-spinner
      display none !important


</style>
